import VueRouter from 'vue-router';
import StartBody from './pages/StartBody.vue';
import SearchBody from './pages/SearchBody.vue';
import VideoBody from './pages/VideoBody';
import ClubBody from "./pages/ClubBody";
import VipBody from "./pages/VipBody";
export default new VueRouter({
    routes:[
        {
            path:"/start",
            component:StartBody
        },
        {
            path:"/search",
            component:SearchBody
        },
        {
            path:"/club",
            component:ClubBody
        },
        {
            path:"/video",
            component:VideoBody
        },
        {
            path:"/vip",
            component:VipBody
        },
    ]
})