<template>
    <div class="Sa">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-empty description="连接失败，请下滑刷新" />
</van-pull-refresh>
 
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {

  data() {
    return {
      count: 0,
      isLoading: false,
    };
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        Toast('连接失败');
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
  },
};

</script>

