<template>
  <div class="sports">
   
<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  <van-cell title="运动目标 10000步"  style=" background-color: white;"/>
    <van-circle v-model="currentRate" :rate="rate" :speed="100" :text="text"  style="margin-top: 1rem;"
    :color="gradientColor"
    />
    <van-picker
  title="切换运动模式"
  show-toolbar
  :columns="columns"
  @cancel="onCancel"
  @confirm="onConfirm"
  v-show="picker"
/>
<van-cell title="步数目标设置"  value="目标10000步"  is-link/>
    <van-cell center title="定位辅助">
  <template #right-icon>
    <van-switch v-model="checked1" size="24" />
  </template>
</van-cell>
<van-cell center title="计步功能">
  <template #right-icon>
    <van-switch v-model="checked2" size="24" />
  </template>
</van-cell>
<van-cell center title="切换模式">
  <template #right-icon>
    <van-switch v-model="picker" size="24" />
  </template>
</van-cell>
<van-cell title="校对日期"  value="校对"  is-link @click="(showdate=true)"/>
<van-datetime-picker
  v-model="currentDate"
  type="date"
  title="选择年月日"
  :min-date="minDate"
  :max-date="maxDate"
  v-show="showdate"
  @cancel="(showdate=false)"
  @confirm="ondate"
/>


<van-cell title="3000s计时器"   is-link @click="(showcd=true)"/>
<div class="cd" v-show="showcd">
  <br>
<van-count-down
  ref="countDown"
  millisecond
  :time="3000"
  :auto-start="false"
  format="ss:SSS"
  @finish="finish"
/>
<br>
<van-grid clickable  :column-num="3" icon-size="30px" square>
  <van-grid-item  icon="play-circle-o" @click="start" />
  <van-grid-item  icon="pause-circle-o" @click="pause" />
  <van-grid-item  icon="replay" @click="reset" />
</van-grid>
<br>
</div>
        </van-pull-refresh>
</div>


</template>

<script>
import { Notify } from 'vant';
import { Toast } from 'vant';
export default {
  data() {
    return {
      count: 0,
      isLoading: false,
      step:7312,
      currentRate: 0,
      gradientColor: {
        '0%': '#3fecff',
        '100%': '#6149f6',
      },
      showcd:false,
      showdate:false,
      checked1:false,
      checked2:true,
      picker:false,
      columns: ['快跑', '步行', '瑜伽', '健身'],
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(2022, 11, 1),
    };
  },
  methods: {
    onConfirm(value, index) {
      console.log(`当前值：${value}, id：${index}`);
      this.picker=false;
    },
    onCancel() {
      Toast('取消');
    },
    ondate(){
      Notify('修改成功');
      this.showdate=false
    },
    start() {
      this.$refs.countDown.start();
    },
    pause() {
      this.$refs.countDown.pause();
    },
    reset() {
      this.$refs.countDown.reset();
    },
    finish() {
      Toast('倒计时结束');
    },
    onRefresh() {
      setTimeout(() => {
        Toast('connecting...');
        let ran=Math.floor((Math.random()*10)+5);
        this.step=this.step+ran
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
  },
  computed: {
    text() {
      return "步数："+this.step;
    },
    rate() {
      return this.step/100;
    },
  },
};
</script>

<style>
.van-grid-item__text {
  font-size: 20px;
}
</style>