        @click-right="(showShare = true)" left-arrow />
      <div class="M11">
        <van-cell-group inset style="margin-top:20px">
          <van-cell title="2022-11" is-link arrow-direction="down" class="month" />
          <van-cell icon="chart-trending-o" title="10,247 Steps" value="11/26" is-link />
          <van-cell icon="chart-trending-o" title="11,389 Steps" value="11/23" is-link />
