          <van-cell icon="chart-trending-o" title="22,234 Steps" value="10/12" is-link />
          <van-cell icon="chart-trending-o" title="13,015 Steps" value="10/11" is-link />
          <van-cell icon="chart-trending-o" title="10,210 Steps" value="10/5" is-link />
