        </van-cell-group>
      </div>
      <div class="M9">
        <van-cell-group inset style="margin-top:20px">
          <van-cell title="2022-9" is-link arrow-direction="down" class="month" />
          <van-cell icon="chart-trending-o" title="12,172 Steps" value="9/29" is-link />
          <van-cell icon="chart-trending-o" title="11,105 Steps" value="9/22" is-link />
          <van-cell icon="chart-trending-o" title="13,293 Steps" value="9/21" is-link />
          <van-cell icon="chart-trending-o" title="10,839 Steps" value="9/19" is-link />
        </van-cell-group>
      </div>
      <div class="M6">
        <van-cell-group inset style="margin-top:20px">
          <van-cell title="2022-6" is-link arrow-direction="down" class="month" />
          <van-cell icon="chart-trending-o" title="9,012 Steps" value="6/27" is-link />
          <van-cell icon="chart-trending-o" title="7,389 Steps" value="6/20" is-link />
          <van-cell icon="chart-trending-o" title="12,390 Steps" value="6/19" is-link />
          <van-cell icon="chart-trending-o" title="8,984 Steps" value="6/16" is-link />
        </van-cell-group>
        <van-share-sheet v-model="showShare" title="分享给朋友" :options="options" @select="onSelect" />
      </div>
    </div>
   
  </div>
</template>

<script>
import { Toast } from 'vant';
import TheEChart from './TheEchart.vue';
export default {
  components:{
    TheEChart,
  },
  data() {
    return {
      minDate: new Date(2022, 5, 1),
      maxDate: new Date(2023, 0, 0),
      showcard:false,
      showhealth: false,
      currentRate: 0,
      showdata: false,
      showbotton: true,
      showShare: false,
      options: [
        { name: 'Wechat', icon: 'wechat' },
        { name: 'Web', icon: 'link' },
        { name: 'Post', icon: 'poster' },
        { name: 'QRcode', icon: 'qrcode' },
      ],
    };
  },
  methods: {
    clickD() {
      this.showdata = true;
      this.showbotton = false
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    formatter(day) {
      const month = day.date.getMonth() + 1;
      const date = day.date.getDate();

      if (month === 11) {
        if (date === 26||date === 23||date === 12||date === 19||date===11||date===9||date===8||date===6) {
          day.topInfo = '已打卡';
        } 
      }
      if (month === 10) {
        if (date === 2||date === 5||date === 11||date === 12||date === 28||date===19||date===20||date===15) {
          day.topInfo = '已打卡';
        } 
      }
      if (month === 9) {
        if (date === 19||date === 21||date === 22||date === 29) {
          day.topInfo = '已打卡';
        } 
      }
      if (month === 6) {
        if (date === 27||date === 20||date === 19) {
          day.topInfo = '已打卡';
        } 
      }


      return day;
    },
  },
  computed: {
    text() {
      return "运动分：" + this.currentRate.toFixed(0);
    },
  },
};
</script>

<style>
